.app {
  text-align: center;
}

nav {
  margin-bottom: 50px;
  background: -webkit-gradient(linear, left top, left bottom, from(#1175d8), to(#163b60));
  background: -webkit-linear-gradient(#1175d8, #163b60);
  background: -o-linear-gradient(#1175d8, #163b60);
  background: linear-gradient(#1175d8, #163b60);
}

div.cart-container {
  max-width: 800px;
}

li {
  margin-top: 15px;
  padding-top: 15px;
  padding-bottom: 15px;
  min-height: 90px;
  min-width: 600px;
  -webkit-box-shadow: 0 3px 5px 0 #ccc;
          box-shadow: 0 3px 5px 0 #ccc;
}
@media only screen and (max-width: 500px) {
  li {
    max-width: 330px;
    min-width: unset;
    margin-right: 50px;
  }
}

img.store-logo {
  width: 50px;
}

button.cart {
  max-height: 29px;
  background: -webkit-gradient(linear, left top, left bottom, from(#1175d8), to(#163b60));
  background: -webkit-linear-gradient(#1175d8, #163b60);
  background: -o-linear-gradient(#1175d8, #163b60);
  background: linear-gradient(#1175d8, #163b60);
  border: none;
}
@media only screen and (max-width: 500px) {
  button {
    width: 5px;
  }
}

button.btn.btn-primary.cart:hover {
  -webkit-box-shadow: inset 0 0 0 2px lightskyblue;
          box-shadow: inset 0 0 0 2px lightskyblue;
}

button.btn.btn-primary.cart.add {
  border-radius: 0 5px 5px 0;
}

button.btn.btn-primary.cart.remove {
  border-radius: 5px 0 0 5px;
}

span.inventory {
  position: absolute;
  left: 0;
  top: 0;
  color: white;
  width: 20px;
  height: 20px;
  background: #1175d8;
  border-top-left-radius: 2px;
  border-bottom-right-radius: 5px;
}

span.inventory > p {
  font-size: 0.8em;
}

input {
  width: 50px;
  text-align: center;
  max-height: 29px;
}

input:focus {
  outline: none;
  border: 2px solid #1175d8;
}

table.table {
  max-width: 600px;
  margin: auto;
  margin-bottom: 50px;
}

th {
  text-align: center;
}

tr.receipt-table {
  color: white;
}

.promo {
  font-size: 0.8em;
  font-style: italic;
  color: green;
}



